@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
  /* width: 980px; */
  /* margin: 0 auto; */
}

.home {
  position: relative;
  height: 5000px;
}

@media (max-width: 750px) {
  .home {
    overflow-x: hidden;
  }
}

.home .section-1 {
  position: relative;
  z-index: 2;
  background-color: white;
  min-width: 90%;
  padding-top: 30px;
}

.home .section-1 > div {
  max-width: 980px;
  width: 90%;
  margin: 0 auto;
  padding: 30px 0 170px 0;
}

.home .section-1 .robot-img {
  width: 740px;
}

.home .section-1 .header {
  font-size: 96px;
  font-weight: 500;
  letter-spacing: -3px;
  line-height: 1;
  color: red;
  text-align: left;
  margin: 0;
}

.home .section-1 .context,
.home .section-1 .link {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.home .section-1 .context {
  font-size: 48px;
  letter-spacing: -0.5px;
  line-height: 1.15;
  text-align: left;
  margin: 35px 0;
}

.home .section-1 .link {
  font-size: 24px;
  color: black;
  text-decoration: none;
  float: left;
  margin-bottom: 1px;
}

.home .section-1 .link:hover {
  text-decoration: underline;
}

/* tablet view */
@media (max-width: 1070px) {
  .home .section-1 > div {
    width: 630px;
  }

  .home .section-1 .header {
    font-size: 64px;
  }

  .home .section-1 .context {
    font-size: 32px;
  }

  .home .section-1 .link {
    font-size: 21px;
  }
}

/* mobile view */
@media (max-width: 750px) {
  .home .section-1 > div {
    width: 87.5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 0 0 70px;
  }

  .home .section-1 .robot-img {
    width: 100vw;
  }

  .home .section-1 .header {
    font-size: 40px;
    letter-spacing: -1px;
    line-height: 1.05;
  }

  .home .section-1 .context {
    font-size: 21px;
    margin: 24px 0 20px 0;
    letter-spacing: -0.5px;
    line-height: 1.2;
  }

  .home .section-1 .link {
    font-size: 17px;
  }
}

.home .section-2 {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  height: 100vh;
}

.home .section-2 .container-1 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 100vh;
  z-index: 6;
}

.home .section-2 .container-1 .header,
.home .section-2 .container-1 .context {
  position: relative;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.home .section-2 .container-1 .header {
  width: 100%;
  font-size: 28px;
  margin: 0;
}

.home .section-2 .container-1 .context {
  width: 100%;
  font-size: 96px;
  line-height: 1.1;
  letter-spacing: -2px;
  margin-top: 30px;
}

.home .section-2 .container-2 {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  -webkit-align-items: center;
          align-items: center;
  top: 0;
  left: 0;
  right: 0;
  width: 980px;
  min-height: 170px;
  height: 30vh;
  margin: auto;
  opacity: 0;
}

.home .section-2 .container-2 .header {
  font-size: 64px;
  font-weight: 500;
  letter-spacing: -1px;
  line-height: 1.1;
  width: 42%;
}

.home .section-2 .container-2 .context {
  width: 58%;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.4;
  margin-top: 15px;
  word-spacing: 1px;
  letter-spacing: 0px;
}

.home .section-2 .container-2 .header,
.home .section-2 .container-2 .context {
  font-family: "Roboto", sans-serif;
  color: black;
  text-align: left;
}

.home .section-2 .shipImg {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  z-index: 5;
}

.home .section-2 .container-3 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  height: 100vh;
  width: 900px;
  margin: 0 auto;
}

.home .section-2 .container-3 > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  width: 180px;
  height: 80vh;
}

.home .section-2 .container-3 .icon {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 170px;
}

.home .section-2 .container-3 .icon > img {
  width: 50px;
  height: 50px;
}

.home .section-2 .container-3 .header {
  color: black;
  font-size: 20px;
  margin-top: 5px;
  width: 100%;
}

.home .section-2 .container-4 {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  z-index: -1;
}

.home .section-2 .container-4 img {
  width: 430px;
}

/* tablet view */
@media (max-width: 1070px) {
  .home .section-2 .container-1 .header {
    font-size: 24px;
  }
  .home .section-2 .container-1 .context {
    font-size: 64px;
    letter-spacing: -1px;
  }

  .home .section-2 .container-2 {
    width: 580px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: center;
            align-content: center;
    height: 30vh;
  }

  .home .section-2 .container-2 .header,
  .home .section-2 .container-2 .context {
    width: 100%;
  }

  .home .section-2 .container-2 .header {
    font-size: 48px;
    margin: 0;
  }

  .home .section-2 .container-2 .context {
    font-size: 17px;
  }

  .home .section-2 .container-3 {
    max-width: 800px;
    min-width: 720px;
    width: calc(100vw - 40px);
    /* height: 500px; */
  }
  /* .home .section-2 .container-3 .left,
  .home .section-2 .container-3 .right {
    width: 170px;
  } */

  .home .section-2 .container-3 > div {
    /* background-color: red; */
    height: 600px;
    width: 125px;
  }

  .home .section-2 .container-3 .header {
    font-size: 18px;
  }

  .home .section-2 .container-4 img {
    width: 380px;
  }
}

/* mobile view */
@media (max-width: 750px) {
  .home .section-2 {
    position: relative;
    height: auto;
  }

  .home .section-2 .container-1 {
    position: relative;
    height: auto;
  }

  .home .section-2 .container-1 .header,
  .home .section-2 .container-1 .context {
    color: black;
  }

  .home .section-2 .container-1 > div {
    width: 87.5%;
    padding: 70px 0 50px 0;
  }

  .home .section-2 .container-1 .header {
    font-size: 19px;
  }

  .home .section-2 .container-1 .context {
    font-size: 40px;
    margin-top: 18px;
  }

  .home .section-2 .shipImg {
    position: relative;
    width: 280px;
    height: 370px;
    margin: 0;
  }

  .home .section-2 .container-2 {
    width: 87.5%;
    position: relative;
    opacity: 1;
    height: auto;
    padding-top: 50px;
  }

  .home .section-2 .container-2 .header {
    font-size: 28px;
    letter-spacing: 0px;
  }

  .home .section-2 .container-2 .context {
    margin-top: 16px;
    line-height: 1.5;
  }

  .home .section-2 .container-3 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    position: relative;
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    width: 87.5%;
    min-width: 320px;
    margin: 40px auto;
    height: auto;
  }

  .home .section-2 .container-3 > div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    height: auto;
    width: 150px;
  }

  .home .section-2 .container-3 .icon img {
    width: 40px;
    height: 40px;
  }

  .home .section-2 .container-3 .header {
    width: 100%;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
  }

  .home .section-2 .container-4 {
    display: -webkit-flex;
    display: flex;
    /* flex-wrap: wrap; */
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    position: relative;
    opacity: 1;
    top: 0;
    left: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    width: 87.5%;
    margin: 0 auto;
  }

  .home .section-2 > .container-4 img:first-child {
    width: 230px;
    height: 320px;
    object-fit: cover;
    margin-right: 10px;
  }

  .home .section-2 > .container-4 img:last-child {
    width: 280px;
    height: 360px;
    object-fit: cover;
    margin-left: 10px;
  }
}

.home .section-3 {
  width: 980px;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  font-size: 100px;
  font-weight: 500;
  padding-top: 4450px;
}

.home .section-3 .container-1 > div {
  width: 650px;
  text-align: left;
}

.home .section-3 .container-1 .header {
  font-size: 28px;
}

.home .section-3 .container-1 .context {
  font-size: 96px;
  line-height: 1.05;
  letter-spacing: -4px;
  margin-top: 30px;
}

/* tablet view */
@media (max-width: 1070px) {
  .home .section-3 {
    width: 670px;
  }

  .home .section-3 .container-1 {
    margin-top: 40px;
  }
  .home .section-3 .container-1 > div {
    width: 450px;
  }
  .home .section-3 .container-1 .header {
    font-size: 24px;
  }
  .home .section-3 .container-1 .context {
    font-size: 64px;
  }
}

/* mobile view */
@media (max-width: 750px) {
  .home .section-3 {
    width: 87.5%;
    padding-top: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .home .section-3 .container-1 {
    text-align: center;
  }

  .home .section-3 .container-1 > div {
    width: auto;
    text-align: center;
  }

  .home .section-3 .container-1 .header {
    font-size: 19px;
  }

  .home .section-3 .container-1 .context {
    font-size: 40px;
    letter-spacing: -1px;
    margin-top: 15px;
    width: 275px;
  }
}

